import React from 'react';

const Marker = () => {
  let groupData = {
    testgroup: {
      identifier1: {
        heading: 0,
        speed: 0,
        username: 'Jimbob',
        position: { latitude: 50, longitude: 10 }
      },
      identifier2: {
        heading: 0,
        speed: 0,
        username: 'Todd Howard',
        position: { latitude: 50.5, longitude: 9.5 }
      },
      identifier3: {
        heading: 0,
        speed: 0,
        username: 'Hodd Toward',
        position: { latitude: 51.5, longitude: 11.5 }
      }
    }
  };

  function appendHTML() {
    // let wrapper = document.createElement('div');
    let html = `<a-scene artoolkit><a-camera gps-camera rotation-reader></a-camera>`;

    const lookupObj = Object.keys(groupData.testgroup);

    lookupObj.forEach((member) => {
      html += `<a-box color="yellow" gps-entity-place="latitude: ${groupData.testgroup[member].position.latitude}; longitude: ${groupData.testgroup[member].position.longitude}"/>`;
    });
    html += `</a-scene>`;
    // console.log(html);
    return { __html: html };
    // document.body.appendChild(wrapper);
  }

  return <div dangerouslySetInnerHTML={appendHTML()}></div>;
};

export default Marker;

